import { AlgaeRecord } from "@livingsnow/record";
import ImageComponent from "components/ImageComponent";
import { ImageContainer, ImageSection } from "components/ImageComponent/styles";
import { useEffect, useMemo, useState, memo } from "react";
import {
  StyledTreeInfo,
  StyledSubText,
  TreeDetailsList,
  TreeListElement,
  TreeDetail,
  TreeDetailKey,
  TreeDetailValue,
  CopyButton,
  Blurb,
  SubTextProps,
} from "./styles";

interface InfoContainerProps {
  record?: AlgaeRecord;
  photoUris?: string[];
  children?: React.ReactNode | React.ReactNode[];
}

const InfoContainer: React.FC<InfoContainerProps> = (
  props: InfoContainerProps
): React.ReactElement => {
  const [recordPhotos, setRecordPhotos] = useState<string[]>();

  var treeDetails: any[] = [];
  var skippedKeys: string[] = ["RecordId", "photoUris", "Photos"];
  for (const [key, value] of Object.entries(props.record ? props.record : {})) {
    if (!skippedKeys.includes(key)) {
      treeDetails.push(
        <TreeListElement key={key}>
          <TreeDetail>
            <TreeDetailKey>
              {"LocationDescription" == key ? "Description" : key}
            </TreeDetailKey>
            <TreeDetailValue>
              {key == "Color" ? JSON.parse(value as string).join(",") : value}
            </TreeDetailValue>
          </TreeDetail>
        </TreeListElement>
      );
    }
  }

  const fetchPhotos = async () => {
    const photoURL =
      "https://snowalgaeproductionapp.azurewebsites.net/api/photos";

    if (props.photoUris) {
      const photoURLs = props.photoUris.map((uri) => `${photoURL}/${uri}`);
      const imageBlobs = await Promise.all(
        photoURLs.map(async (url) => await fetch(url).then((res) => res.blob()))
      );
      const objectURLs = imageBlobs.map((blob) => URL.createObjectURL(blob));
      setRecordPhotos(objectURLs);
    } else {
      setRecordPhotos(undefined);
    }
  };
  useEffect(() => {
    // const makePhotos = async () => {
    //     const photoURL =
    //   "https://snowalgaeproductionapp.azurewebsites.net/api/photos";
    //     const photos = props.photoUris?.map((uri) => `${photoURL}/${uri}`);
    //     await setRecordPhotos(photos);
    // }
    fetchPhotos();
    // console.log("photoUris called")
  }, [props.record]);

  const images = recordPhotos?.map((photoUrl) => (
    <ImageContainer key={photoUrl} src={photoUrl}></ImageContainer>
  ));
  return (
    <StyledTreeInfo>
      <TreeDetailsList>{treeDetails}</TreeDetailsList>
      {images && <ImageComponent images={images} />}
      {props.children}
    </StyledTreeInfo>
  );
};

export default InfoContainer;
