import InfoPanel from "components/InfoPanel/index";
import { useState, useEffect, useCallback, useRef, useMemo } from "react";
import Map, { Source, Layer, MapRef, ViewState } from "react-map-gl";
import { EventData, MapLayerMouseEvent } from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import bbox from "@turf/bbox";
import InfoContainer from "components/InfoContainer";
import { ToolTip, layerStyle, highlightStyle } from "./styles";
import { Blurb } from "components/InfoContainer/styles";
import { useWindowSize } from "hooks/useWindowSize";
import { useMeasure } from "@react-hookz/web";

const LAYER_NAME = "records";

function RecordsMap() {
  const mapRef = useRef<MapRef>(null);
  //   const infoPanelRef = useRef<HTMLDivElement>(null);
  const [allData, setAllData] = useState<any>();
  const [hoverInfo, setHoverInfo] = useState<any>();
  const [isInfoPanelExpanded, setIsInfoPanelExpanded] = useState(true);
  const [selected, setSelected] = useState<any>();
  const [viewState, setViewState] = useState<ViewState>({
    latitude: 48.7339,
    longitude: -122.4869,
    zoom: 5,
    bearing: 0,
    pitch: 0,
    padding: { left: 100, top: 100, right: 100, bottom: 100 },
  });

  // window geometry related hooks
  const isNarrow = useWindowSize(600);
  //   const { width, height } = useContainerDimensions(infoPanelRef);
  const [measurements, infoPanelRef] = useMeasure<HTMLDivElement>();

  // memoized filters
  const selectedRecord = (selected && selected.properties.RecordId) || "";
  const highlightFilter = useMemo(
    () => ["==", ["get", "RecordId"], selectedRecord],
    [selectedRecord]
  );

  useEffect(() => {
    fetch(
      "https://snowalgaeproductionapp.azurewebsites.net/api/v2/records/geojson"
    )
      .then((resp) => resp.json())
      .then((json) => setAllData(json.data))
      .catch((err) => console.error("Could not load data", err)); // eslint-disable-line
  }, []);

  const onHover = useCallback((event: any) => {
    const {
      features,
      point: { x, y },
    } = event;
    const hoveredFeature = features && features[0];

    // prettier-ignore
    setHoverInfo(hoveredFeature && {feature: hoveredFeature, x, y});
  }, []);

  const onSelectZoom = (event: MapLayerMouseEvent) => {
    const feature = event.features && event.features[0];

    if (feature) {
      // calculate the bounding box of the feature
      const [minLng, minLat, maxLng, maxLat] = bbox(feature);

      var options = {
        padding: { left: 100, top: 100, right: 100, bottom: 100 },
        maxZoom: viewState && viewState.zoom > 10 ? viewState.zoom : 10,
        linear: true,
        duration: 650,
      };

      if (mapRef.current) {
        var { longitude, latitude, zoom }: EventData = mapRef.current.fitBounds(
          [
            [minLng, minLat],
            [maxLng, maxLat],
          ],
          options
        );

        zoom = zoom < viewState.zoom ? zoom : viewState.zoom;

        // update the viewport
        setViewState({
          ...viewState,
          longitude,
          latitude,
          zoom,
        });

        if (!isInfoPanelExpanded) {
          setIsInfoPanelExpanded(true);
        }
      }
    }

    setSelected(feature || null);
    if (infoPanelRef && infoPanelRef.current)
      infoPanelRef.current.scrollTo(0, 0);
  };

  const handleToggleInfoPanel = useCallback(
    () => setIsInfoPanelExpanded(!isInfoPanelExpanded),
    [isInfoPanelExpanded]
  );

  useEffect(() => {
    /**
     * Padding in fitBounds and padding here do not appear to be the same.
     * Directly accessing the Map within the DOM and using easeTo seems to be the
     * only way I can utilize padding to center viewport from the user's perspective
     * as described here: https://github.com/mapbox/mapbox-gl-js/pull/8638
     */
    if (mapRef && mapRef.current && measurements)
      isInfoPanelExpanded
        ? mapRef.current.getMap().easeTo({
            padding: isNarrow
              ? { left: 100, top: 100, right: 100, bottom: measurements.height }
              : { left: measurements.width, top: 100, right: 100, bottom: 100 },
          })
        : mapRef.current.getMap().easeTo({
            padding: isNarrow
              ? { left: 100, top: 100, right: 100, bottom: 0 }
              : { left: 0, top: 100, right: 100, bottom: 100 },
          });
  }, [measurements?.width, handleToggleInfoPanel]);

  return (
    <>
      <Map
        ref={mapRef}
        initialViewState={viewState}
        mapStyle="mapbox://styles/mapbox/outdoors-v12"
        style={{ width: "100vw", height: "100vh" }}
        mapboxAccessToken={
          "pk.eyJ1IjoidGNvd2FuIiwiYSI6ImNsY3MxeW92ejBsaGozcHFxbm9hazM4aTgifQ.KR_WuLjBMjU4QSOR7TV9eg"
        }
        interactiveLayerIds={[LAYER_NAME]}
        onMouseMove={onHover}
        onClick={onSelectZoom}
        // https://visgl.github.io/react-map-gl/docs/get-started/state-management
        onMove={(event: any) => setViewState(event.viewState)}
      >
        <Source type="geojson" data={allData}>
          <Layer {...layerStyle} />
          <Layer {...highlightStyle} filter={highlightFilter} />
        </Source>
        {hoverInfo && (
          <ToolTip x={hoverInfo.x} y={hoverInfo.y}>
            <div>Name: {hoverInfo.feature.properties.Name}</div>
            <div>Date: {hoverInfo.feature.properties.Date}</div>
            <div>Type: {hoverInfo.feature.properties.Type}</div>
          </ToolTip>
        )}
        <InfoPanel
          // @ts-ignore
          ref={infoPanelRef}
          title={"Living Snow Project"}
          className={"info-panel"}
          isExpanded={isInfoPanelExpanded}
          handleToggle={handleToggleInfoPanel}
          color={
            selected &&
            selected.layer.id == LAYER_NAME &&
            selected.properties.Color
              ? JSON.parse(selected.properties.Color)[0]
              : "red"
          }
        >
          {selected && selected.layer.id == LAYER_NAME && (
            <InfoContainer
              record={selected.properties}
              photoUris={
                selected.properties.Photos
                  ? JSON.parse(selected.properties.Photos).map(
                      (p: any) => p.uri
                    )
                  : undefined
              }
            />
          )}
          {!selected && (
            <InfoContainer>
              <Blurb>
                This is a quick proof-of-concept webmap illustrating what can be
                done with the new GeoJSON endpoint. <b>Click on points</b> to
                pull up the record details and images (if available). Purple
                points are <b>sightings</b> while blue are <b>samples</b>.
              </Blurb>
              <Blurb>
                This side-panel can be collapsed by clicking on the
                double-chevron button.
              </Blurb>
            </InfoContainer>
          )}
        </InfoPanel>
      </Map>
    </>
  );
}

export default RecordsMap;
